import React, { Component } from 'react';
import routeConstant from 'routes/route/routeConstant';
import { Link } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { BtnAddNew } from 'components/common/button';
import { Card, Row, Col, Checkbox, message } from 'antd';
import { moneyToString } from 'utils/formatUtil';
import { getAccountsReceivable, deleteAccountReceivable } from 'services/accountsReceivable';

class AccountsReceivable extends Component {
  state = { reload: 0 };

  getAccountsReceivableAddIndex = async () => {
    const res = await getAccountsReceivable();

    const newItems = res.data.items.map((value, index) => {
      const newValue = value;
      newValue.index = index + 1;
      return newValue;
    });
    res.data.items = newItems;
    return res;
  };

  handleDelete = async id => {
    try {
      await deleteAccountReceivable(id);
      message.success('Xa thnh cng');
      this.setState({
        reload: this.state.reload + 1,
      });
    } catch (err) {
      console.log(err);
    }
  };

  render() {
    const columns = [
      {
        title: 'STT',
        dataIndex: 'index',
        key: 'index',
        width: '100px',
      },
      {
        title: 'Tn khon thu',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Hc ph',
        dataIndex: 'isFee',
        render: (text, record) => <Checkbox checked={record.isFee} />,
      },
      {
        title: 'Kinh ph T',
        dataIndex: 'isTrainExpense',
        render: (text, record) => <Checkbox checked={record.isTrainExpense} />,
      },
      {
        title: 'Hc li',
        dataIndex: 'isRestudy',
        render: (text, record) => <Checkbox checked={record.isRestudy} />,
      },
      {
        title: 'Thi li',
        dataIndex: 'isRetest',
        render: (text, record) => <Checkbox checked={record.isRetest} />,
      },
      {
        title: 'S tin',
        dataIndex: 'amount',
        render: text => moneyToString(text),
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: routeConstant.DANH_MUC__TAI_CHINH__KHOAN_THU__NHAP_DU_LIEU,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];

    return (
      <Card title="Danh mc khon thu">
        <CartHeadAction>
          <Link to={routeConstant.DANH_MUC__TAI_CHINH__KHOAN_THU__NHAP_DU_LIEU}>
            <BtnAddNew />
          </Link>
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              columns={columns}
              service={this.getAccountsReceivableAddIndex}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <AccountsReceivable />
  </Page>
);



